import { useState } from "react";
import './reset.css';
import './main.css';

function CalTest(){
    const [calc, setCalc] = useState("");
    const [operCheck, setOperCheck] = useState(true);
    const [pointCheck, setPointCheck] = useState(true);

    const getNum = (e) => {
        setCalc((prev) => prev + e.target.value);
        setOperCheck(true);
      };

      const getOper = (e) => {
        if (operCheck) {
          setCalc((prev) => prev + e.target.value);
          setOperCheck(false);
        }
      };

      const getPoint = (e) => {
        if (calc.length === 0) {
          return;
        }
        if (pointCheck) {
          setCalc((prev) => prev + e.target.value);
         // setPointCheck(false);
        }
      };

      const getResult = () => {
        // let replace_str = calc.replace(/x/gi, "*").replace(/÷/gi, "/");
        let replace_str = calc.replace(/x/gi, "*");
    
        if (isNaN(eval(replace_str))) {
          setCalc("");
        } else if (eval(replace_str) == Infinity) {
          alert("0으로 나눌수 없습니다.");
          setCalc("");
          return false;
        } else {
          setCalc((prev) => eval(replace_str));
        }
      };


      const delCalc = () => {
        setPointCheck(true);
        setOperCheck(true);
        let str = String(calc).slice(0, -1);
        setCalc((prev) => str);
      };

      const allClear = () => {
        setPointCheck(true);
        setCalc((prev) => "");
      };



    return (
     <>
      <input readOnly value={calc} />
      <div className="buttonbox">
      <button onClick={allClear}>AC</button>
      <button onClick={delCalc}>DEL</button>
      <button value="%" onClick={getOper}>%</button>  
      <button value="/" onClick={getOper}>/</button>
      <button value={7} onClick={getNum}> 7 </button>
      <button value={8} onClick={getNum}> 8 </button>
      <button value={9} onClick={getNum}> 9 </button>
      <button value="x" onClick={getOper}> x </button>
      <button value={4} onClick={getNum}> 4 </button>
      <button value={5} onClick={getNum}> 5 </button>
      <button value={6} onClick={getNum}> 6 </button>
      <button value="-" onClick={getOper}> - </button>
      <button value={1} onClick={getNum}> 1 </button>
      <button value={2} onClick={getNum}> 2 </button>
      <button value={3} onClick={getNum}> 3</button>
      <button value="+" onClick={getOper}> +</button>
      <button value={0} onClick={getNum}> 0 </button>
      <button value="." onClick={getPoint}> . </button>
      <button className="gg" onClick={getResult}>=</button>
      </div>
     </>
    )
}
export default CalTest;